.modal {
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    opacity: 0;
    pointer-events: none;
};


.modal_active {
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    
};

.modal_content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s;
    width: 80vw;
};

.modal_content_active {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    transform: scale(1);
    transition: 0.4s;
    width: 80vw;
};

.photo {
    width: 100px;
    height: 100px;
};

.title {
    margin-top: 5px;
};

.description {
    font-size: 10px;
}